
.list {
    border-radius: 5px;
    height: 600px;
    margin-top: 1%;
    overflow: auto;
    background: rgb(104, 142, 255);
}
::-webkit-scrollbar {
    width: 5px;
}
::-webkit-scrollbar-track {
    background: #f1f1f1;
}
::-webkit-scrollbar-thumb {
    background: #888;
}
.fa-trash {
    float: right;
    color: rgb(235, 15, 15);
}
.fa-trash:hover{
   color: rgb(255, 155, 155);
}
.inline {
    display: inline;
}

/* .task{
    background-color: #2b2a2ab7; 
    color: white;
} */
.list-text-color {
    color: white;
}
.list-header {
    /* background-color: #9796965e; */
    padding: 1rem;
}
.margin {
    margin: 1rem;
}
.the-task {
    margin-top: 0.5rem;
}



